<template>
  <div class="login-container pull-height">
    <div class="bgDiv">
      <div class="login_components">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px" class="demo-ruleForm">
          <div class="login_text">
            <el-input v-model="ruleForm.userName"  placeholder="账号" class="login_border">
              <i slot="prefix" class="iconfont icon-yonghu login_icon"></i>
            </el-input>
            <el-input type="password" v-model="ruleForm.password"  placeholder="密码" @keyup.enter.native="submitForm('ruleForm')">
              <i slot="prefix" class="iconfont icon-mima login_icon"></i>
            </el-input>
          </div>
          <!-- <div class="forget">
            <el-checkbox class="forget_zhanghao">记住账号</el-checkbox>
            <h6>修改密码</h6>
          </div> -->
          <div class="login_div">
            <el-button type="primary" class="submitBtn" @click="submitForm('ruleForm')">登录</el-button>
          </div>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'login',
  data () {
    return {
      ruleForm: {
        userName: this.defaultUserName,
        password: this.defaultPassword
        // userName: 'admin',
        // password: '0'
      },
      rules: {
        userName: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      },
      obj: {}
    }
  },
  // created: function () {
  //   //  主页添加键盘事件,注意,不能直接在焦点事件上添加回车
  //   this.$nextTick(() => {
  //     var that = this
  //     var explorer = window.navigator.userAgent.toLowerCase() // 取得浏览器的userAgent字符串
  //     document.onkeydown = function (e) {
  //       if (explorer.indexOf('firefox') >= 0) { // 火狐回车事件
  //         var key = e.which
  //       } else {
  //         key = window.event.keyCode
  //       }
  //       if (key === 13) {
  //         that.submitForm('ruleForm')
  //       }
  //     }
  //   })
  // },
  mounted: function () {
    var ua = navigator.userAgent
    var explorer = window.navigator.userAgent.toLowerCase() // 取得浏览器的userAgent字符串
    var isOpera = explorer.indexOf('Opera') >= 0
    if (ua.indexOf('Windows NT 5') !== -1) {
      alert('该软件不支持当前XP系统,请您更新系统!')
      window.location.href = '/scan.html'
    } else {
      if (explorer.indexOf('firefox') >= 0) { // 判断是否Firefox浏览器
        var version = explorer.match(/firefox\/([\d.]+)/)[1]
        version = parseInt(version)
        if (version < 52) {
          alert('您当前使用的IE浏览器和软件不匹配，请更新该火狐浏览器')
          window.location.href = '/scan.html'
        }
      } else if (explorer.indexOf('chrome') >= 0) {
        version = explorer.match(/chrome\/([\d.]+)/)[1]
        version = parseInt(version)
        if (version < 49) {
          alert('您当前使用的IE浏览器和软件不匹配，请更新该谷歌浏览器')
          window.location.href = '/scan.html'
        }
      } else {
        if (explorer.indexOf('compatible') >= 0 && explorer.indexOf('msie') >= 0 && !isOpera) { // 判断是否IE浏览器
          alert('您当前使用的IE浏览器和软件不匹配，请重新更换或者下载火狐(谷歌)浏览器')
          window.location.href = '/scan.html'
        }
        alert('您当前使用的浏览器和软件不匹配，请重新更换或者下载火狐(谷歌)浏览器')
        window.location.href = '/scan.html'
      }
    }
  },
  methods: {
    submitForm (formName) {
      let that = this
      this.$refs[formName].validate(function (valid) {
        if (valid) {
          that.$axios.post(that.api + '/Login' + that.actionExt, {
            userName: that.ruleForm.userName,
            password: that.ruleForm.password
          }).then(function (response) {
            if (response.data.code === 0) {
              that.TOKEN = response.data.data
              that.$router.push({
                name: 'archivelist'
              })
            } else {
              that.$message.error(response.data.errMsg)
            }
          }).catch(function (error) {
            console.log(error)
            // if (error.response) {
            //   var status = error.response.status.toString()
            //   console.log('转转类型之后', typeof (status))
            //   if (status === '50') {
            //     that.$message.error('服务器在尝试处理请求时发生内部错误')
            //   } else if (status.substring(0, 1) === '4') {
            //     that.$message.error('请求出错，可能是服务器未找到页面')
            //   } else {
            //     that.$message.error('未知错误')
            //   }
            // }
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss">
.login-container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: rgba(0, 0, 0, 0);
  position: relative;
}
.login-container::before {
  z-index: -999;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: url("/static/assets/bg-pic.jpg");
  background-size: cover;
}
.pull-height {
  height: 100%;
}
.bgDiv {
  position: relative;
  width: 980px;
  height: 600px;
  background-image: url("/static/assets/images/login_border.png");
}
.login_components {
  width: 254px;
  height: 280px;
  position: absolute;
  right: 8%;
  top: 38%;
}
.login_text {
  width: 100%;
  height: 100px;
  background-color: #ffffff;
  border-radius: 6px;
  box-shadow: #ccc 0px 0px 30px 1px;
}
.demo-ruleForm {
  width: 100%;
  margin: 0 auto;
}
.demo-ruleForm .el-input {
  width: 75%!important;
  margin-left: 12%;
}
.demo-ruleForm .el-input__inner {
  width: 100%;
  height: 49px!important;
  line-height: 50px!important;
}
.demo-ruleForm .el-input__inner {
  border-color: #ffffff;
}
.demo-ruleForm .el-input__inner:focus {
  border-color: #ffffff!important;
  outline: 0;
}
.demo-ruleForm .el-input__prefix {
  top: 16px!important;
}
.login_icon {
  color: #999999;
  font-size: 12px;
}
.login_border {
  border-bottom: 1px solid #d6d6d6;
}
.forget {
  margin-top: 20px;
}
.forget .forget_zhanghao {
  margin-left: 15px;
  visibility: hidden;
}
.forget h6 {
  margin: 0;
  float: right;
  margin-right: 15px;
  color: #999;
  cursor: pointer;
}
.forget h6:hover {
  text-decoration: underline;
}
.login_div {
  width: 164px;
  height: 44px;
  margin: 22% auto 0px auto;
  border-radius: 6px;
}
.login_div .submitBtn {
  width: 100%;
  height: 100%;
  background-color: #228ef6;
  border-radius: 6px;
  padding: 0px;
  font-size: 14px;
}
</style>



